import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Nick from './panels/Nick';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			error_t: null,
			statusname: null,
			btnshow: false,
			countname: 0,
			newsearchname: null,
			vkid: undefined,
			yourname: null,
			idyourname: null,
			datayourname: null,
			loading: true
		};
	}
	
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({
						fetchedUser: e.detail.data,
						vkid: e.detail.data.id,
						profilename: e.detail.data.first_name,
						profilesurname: e.detail.data.last_name,
						profileimg: e.detail.data.photo_200
					});
					break;
				default:
					console.log(e.detail.type);
			}
			
		});
		connect.send('VKWebAppGetUserInfo', {});
		
		setTimeout(this.scannames, 2000);
	}

	go = (e) => {
		this.setState({ 
			activePanel: e.currentTarget.dataset.to,
			error_t: null,
			statusname: null,
			btnshow: false
		})
	};
	
	searchname = async (e) => {
		let search_name = document.getElementById('nickname');
		
		this.setState({
			newsearchname: search_name.value
		});
	
		if(search_name.value === "") {
			this.setState({
				error_t: "Введите никнейм",
				statusname: "error",
				btnshow: false
			});
		} else {
			const api_url = await
			fetch(`https://yournickname.app7144334.vk.showhim.net/api/searchname.php?nickname=`+search_name.value);
			const data = await api_url.json();
			
			if(data.code === 0) {
				this.setState({
					error_t: "Данный никнейм не занят вы можете зарегистрировать его!",
					statusname: "valid",
					btnshow: true
				});
			} else if(data.code === 1) {
				this.setState({
					error_t: "Данный никнейм занят, вы можете просмотреть его страницу!",
					statusname: "error",
					btnshow: 2
				});
			}
		}
	};
	
	scannames = async (e) => {
		const api_url = await
			fetch(`https://yournickname.app7144334.vk.showhim.net/api/getyounames.php?vkid=`+this.state.vkid);
			const data = await api_url.json();
			console.log(data);
			
		this.setState({ loading: false });
		
		if(data.length > 0) {
			this.setState({
				countname: data.length,
				yourname: data[0].nickname,
				idyourname: data[0].id,
				datayourname: data[0].data
			});
		}

	};
	
	openyourname = async (e) => {
		this.go(e);
	};
	
	registername = async (e) => {
	let search_name = document.getElementById('nickname');		
		if(this.state.countname > 0) {
			this.setState({
				error_t: "Вы не можете зарегистрировать больше одного никнейма",
				statusname: "error"
			});
		} else {
			const api_url = await
			fetch(`https://yournickname.app7144334.vk.showhim.net/api/create.php?vkid=`+this.state.vkid+`&nickname=`+this.state.newsearchname);
			const data = await api_url.json();
			if(data.code === 2) {
				this.setState({
					error_t: "Вы зарегистрировали свой никнейм успешно!",
					statusname: "valid",
					countname: 1,
					yourname: search_name.value.replace( /\s/g, ''),
					btnshow: false
				});
			this.scannames();
			}
			if(data.code === 1) {
				this.setState({
					error_t: "Что-то пошло не так",
					statusname: "error"
				});
			}
		}
	};
	
	removeyourname = async (e) => {
		const api_url = await
		fetch(`https://yournickname.app7144334.vk.showhim.net/api/removename.php?nameid=`+this.state.idyourname);
		const data = await api_url.json();
		if(data.code === 3) {
			this.setState({
				error_t: "Ваш никнейм удален!",
				statusname: "valid",
				countname: 0
			});
		}
		if(data.code === 4) {
			this.setState({
				error_t: "Что-то пошло не так",
				statusname: "error"
			});
		}
	};
	
	showprofilename = async (e) => {
		this.go(e);
		const api_url = await
		fetch(`https://yournickname.app7144334.vk.showhim.net/api/infoname.php?nickname=`+this.state.newsearchname);
		const data = await api_url.json();
		this.setState({
			searchnickname: data.nickname,
			searchdata: data.data,
			searchcreateid: data.createid,
			searchid: data.id,
			namesurname: data.name,
			photo200: data.photo
		});
		if(this.state.photo200 != null)
			this.setState({ loading: false })
	};

	
	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" 
					go={this.go} 
					searchname={this.searchname}
					error_t={this.state.error_t}
					statusname={this.state.statusname}
					btnshow={this.state.btnshow}
					countname={this.state.countname}
					yourname={this.state.yourname}
					openyourname={this.openyourname}
					registername={this.registername}
					removeyourname={this.removeyourname}
					showprofilename={this.showprofilename}
					loading={this.state.loading}
				/>
				<Persik id="persik" 
					fetchedUser={this.state.fetchedUser} 
					go={this.go}
					yourname={this.state.yourname}
					vkid={this.state.vkid}
					datayourname={this.state.datayourname}
					idyourname={this.state.idyourname}
					profilename={this.state.profilename}
					profilesurname={this.state.profilesurname}
					profileimg={this.state.profileimg}
				/>
				<Nick id="nick" 
					go={this.go}
					newsearchname={this.state.newsearchname}
					searchnickname={this.state.searchnickname}
					searchdata={this.state.searchdata}
					searchcreateid={this.state.searchcreateid}
					searchid={this.state.searchid}
					namesurname={this.state.namesurname}
					photo200={this.state.photo200}
					loading={this.state.loading}
				/>
			</View>
		);
	}
}

export default App;
