import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div } from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Persik = (props,yourname,datayourname,idyourname,fetchedUser,ListItem,Avatar) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			{props.yourname}
		</PanelHeader>
		
		<Group title="Информация о никнейме">
			<Div>
				<Div>Никнейм: {props.yourname}</Div>
				<Div>Дата регистрации: {props.datayourname}</Div>
				<Div>Автор: {props.profilesurname} {props.profilename}</Div>
			</Div>
		</Group>
		<Group title="Карточка никнейма">
			<Div className="card">
				<Div className="card-info">
					<span className="card-info-name">{props.yourname}</span>
					<span className="card-info-avatar">
						<img alt="" src={props.profileimg} />
					</span>
					<span className="card-info-data"><small>Дата:</small>{props.datayourname}</span>
					<span className="card-info-id"><small>Номер:</small>{props.idyourname}</span>
					<span className="card-info-downname">{props.profilesurname} {props.profilename}</span>
				</Div>
			</Div>
		</Group>
	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Persik;
