import React from 'react';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Spinner } from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Persik = (props,newsearchname) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			{props.newsearchname}
		</PanelHeader>
		{props.loading === true &&
		<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
			<Spinner size="large" style={{ marginTop: 20 }} />
		</div>
		}
		{props.loading === false &&
		<div>
			<Group title="Информация о никнейме">
				<Div>
					<Div>Никнейм: {props.newsearchname}</Div>
					<Div>Дата регистрации: {props.searchdata}</Div>
					<Div>Автор: {props.namesurname}</Div>
				</Div>
			</Group>
			<Group title="Карточка никнейма">
				<Div className="card">
					<Div className="card-info">
						<span className="card-info-name">{props.newsearchname}</span>
						<span className="card-info-avatar">
							<img alt="" src={props.photo200} />
						</span>
						<span className="card-info-data"><small>Дата:</small>{props.searchdata}</span>
						<span className="card-info-id"><small>Номер:</small>{props.searchid}</span>
						<span className="card-info-downname">{props.namesurname}</span>
					</Div>
				</Div>
			</Group>
		</div>
		}
	</Panel>
);

export default Persik;
