import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Button, Group, Div, PanelHeader, FormLayout, Input, Spinner } from '@vkontakte/vkui';

import Icon16Chevron from '@vkontakte/icons/dist/16/chevron';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';

const Home = ({ props, id, go, searchname, error_t, nickname, statusname, btnshow, countname, yourname, openyourname, registername, removeyourname, showprofilename, loading }) => (
	<Panel id={id}>
		<PanelHeader>Твой никнейм</PanelHeader>
		
		<Group>
			<FormLayout>
            <Input
              type="text"
              top="Введите никнейм для поиска"
			  bottom={error_t}
              id="nickname"
              value={nickname}
			  status={statusname}
			  maxLength="16"
            />
			
			{btnshow === true &&
				<Button size="xl" onClick={registername}>Зарегистрировать никнейм</Button>
			}
			{btnshow === 2 &&
				<Button size="xl" onClick={showprofilename} data-to="nick">Просмотреть никнейм</Button>
			}
			<Button size="xl" onClick={searchname}>Найти</Button>
          </FormLayout>
		</Group>
		
		<Group title="Ваш зарегистрированный никнейм">
		{loading === true &&
		<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
			<Spinner size="large" style={{ marginTop: 20, marginBottom: 20 }} />
			
		</div>
		}
		{countname === 0 && loading === false &&
			<Div>К сожалению у Вас пока что нет никнейма, Вы можете написать свой никнейм в поиск если он не занят зарегистрировать его!</Div>
		}
		{countname > 0 &&
				<Div style={{display: 'flex', justifyContent: 'space-between'}}>
					<Div style={{ marginRight: 10, display: 'flex', alignItems: 'center' }}>1. {yourname}</Div>
					<Div style={{ marginRight: 10, display: 'flex', justifyContent: 'end' }}>
						<Button onClick={removeyourname} style={{ marginRight: 5 }} level="destructive"><Icon16Cancel/></Button>
						<Button onClick={openyourname} data-to="persik"><Icon16Chevron/></Button>
					</Div>
				</Div>
		}
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
